import React, { SVGProps, useState, useEffect } from 'react';
import { connect } from 'redux-zero/react';
import Draggable from 'react-draggable';
import { IPageProps } from '.';
import { CompDigit } from '../components/CompDigit';
import { CompEvaluation } from '../components/CompEvaluation';
import { CompAddition } from '../components/CompAddition';
import { connectDigit, connectEvaluation } from '../common/redux';
import { Util } from '../common/utils';
import { Asset } from '../common/defines';
import logo from './draggable_4.svg';

interface IPageMathProps extends IPageProps {
  finished: boolean;
  session: number;
  digitPanel: any;
  targetPanel: any;
  updateScore: any;
}

const FC: React.FC<IPageMathProps> = (props) => {
  const [_, refresh] = React.useState(0);
  const { finished, session, digitPanel, targetPanel, updateScore } = props;
  const problems = React.useMemo(
    () => [...Array(Asset.SIZE)].map(() => [Util.random(Asset.MIN, Asset.MAX), Util.random(Asset.MIN, Asset.MAX)]),
    [session]
  );

  return (
    <div key={session}>
      {/* <object data={logo} /> */}
      <div style={{ display: 'flex', position: 'fixed', backgroundColor: 'transparent', zIndex: 999 }}>
        {[...Array(10)].map((_, i) => (
          <CompDigit
            key={i}
            digit={i}
            draggable={!finished}
            onDragStart={(event: any) => targetPanel(null)}
            onDragEnd={(event: any) => {
              if (digitPanel) {
                const { validDigit, panel } = digitPanel;
                panel.innerHTML = '';
                panel.appendChild(Util.cloneDigit(event.target, validDigit, updateScore));
                refresh(Math.random());
              }
            }}
          />
        ))}
      </div>
      <CompEvaluation />
      <div className="content-panel">
        <div style={{ display: 'flex', flexFlow: 'wrap', backgroundColor: 'linen' }}>
          {problems.map((operands, i) => (
            <CompAddition key={i} id={String(i)} operands={operands} />
          ))}
        </div>
      </div>
    </div>
  );
};

export const PageMath = connectEvaluation(connectDigit(FC));
