import React, { SVGProps, useState, useEffect } from 'react';
import { connect } from 'redux-zero/react';
import Draggable from 'react-draggable';
import { IPageProps } from '.';
import { CompDigit } from '../components/CompDigit';
import { CompEvaluation } from '../components/CompEvaluation';
import { CompAddition } from '../components/CompAddition';
import { connectDigit, connectEvaluation } from '../common/redux';
import { Util } from '../common/utils';
import { Asset } from '../common/defines';
import logo from './draggable_4.svg';

interface IPageMathProps extends IPageProps {
  finished: boolean;
  session: number;
  digitPanel: any;
  targetPanel: any;
  updateScore: any;
}

const FC: React.FC<IPageMathProps> = (props) => {
  const { finished, session, digitPanel, targetPanel, updateScore } = props;

  fetch('https://api.dictionaryapi.dev/api/v2/entries/en_US/hello', {
    method: 'GET',
    headers: {
      'Content-Type': 'application/json',
    },
  }).then((res) => console.log({ res }));

  return <div key={session}></div>;
};

export const PageWelcome = connectEvaluation(connectDigit(FC));
