import React from 'react';

interface ICompDigitProps extends React.SVGProps<SVGSVGElement> {
  digit: number;
  draggable: boolean;
  onDragStart?: any;
  onDragEnd?: any;
}

export const CompDigit: React.FC<ICompDigitProps> = ({ digit, draggable, onDragStart, onDragEnd, ...props }) => {
  return (
    <div draggable={draggable} onDragStart={onDragStart} onDragEnd={onDragEnd} className="digit">
      {digit}
    </div>
  );
};
