import React from 'react';
import { connectEvaluation } from '../common/redux';

declare var MediaRecorder: any;
interface ICompRecorderProps extends React.SVGProps<SVGSVGElement> {
  word: string;
}

export const CompRecorder: React.FC<ICompRecorderProps> = (props) => {
  const [recording, setRecording] = React.useState(false);
  const { word } = props;
  const recorderEl = React.useMemo(() => {
    async function initialize() {
      const chunks: any[] = [];

      const blobToBase64 = async (blob: any): Promise<string> =>
        new Promise((resolve) => {
          var reader = new FileReader();
          reader.onload = function () {
            var dataUrl = reader.result as string;
            var [_, base64] = dataUrl?.split(',');
            resolve(base64);
          };
          reader.readAsDataURL(blob);
        });

      const stream: MediaStream = await navigator.mediaDevices.getUserMedia({ audio: true });
      const recorder = new MediaRecorder(
        stream
        // { mimeType: 'video/webm; codecs=vp9' }
      );
      recorder.ondataavailable = async (e: any) => {
        chunks.push(e.data);
        if (recorder.state === 'inactive') {
          try {
            const blob = new Blob(chunks, { type: 'audio/webm' });
            // audioEl.src = URL.createObjectURL(blob);
            // audioEl.play();

            const data = await blobToBase64(blob);
            // console.log({ chunks, data });
            const res = await fetch('https://speech.googleapis.com/v1/speech:recognize', {
              method: 'POST',
              headers: {
                'Content-Type': 'application/json',
                key: 'AIzaSyAWEriX0ahrPBMHozN9zCfqxuyCwxVBFhs',
              },
              body: JSON.stringify({
                audio: {
                  content: data,
                },
                config: {
                  enableAutomaticPunctuation: false,
                  encoding: 'LINEAR16',
                  languageCode: 'en-US',
                  sampleRateHertz: 16000,
                  maxAlternatives: 30,
                },
              }),
            });
            console.log({ res });
          } catch (error) {
            console.log(error);
          }
        }
      };
      recorder.onstop = () => (chunks.length = 0);
      return recorder;
    }
    const audioEl = document.createElement('audio');
    // const sourceEl = document.createElement('source');
    // sourceEl.type = 'audio/webm';
    // audioEl.appendChild(sourceEl);
    return initialize();
  }, [word]);

  return (
    <div
      style={{
        width: 180,
        textAlign: 'center',
        zIndex: 999,
      }}
    >
      <div
        className="button"
        style={{
          backgroundImage: `url(${process.env.PUBLIC_URL}/${recording ? 'recording' : 'recorder'}.png)`,
        }}
        onClick={() =>
          recorderEl.then((recorder) => {
            setRecording(recorder.start() || true);
            setTimeout(() => setRecording(recorder.stop() || false), 2000);
          })
        }
      />
    </div>
  );
};
