import React from 'react';
import { connectEvaluation } from '../common/redux';
import { CompNumber } from './CompNumber';

interface ICompAdditionProps extends React.SVGProps<SVGSVGElement> {
  operands: number[];
  finished: boolean;
  updateResult: any;
}

const FC: React.FC<ICompAdditionProps> = ({ id, operands, finished, updateResult, ...props }) => {
  const summandFirst = React.useRef<HTMLDivElement>(null);
  const summandSecond = React.useRef<HTMLDivElement>(null);
  const sum = React.useRef<HTMLDivElement>(null);
  const [operandFirst, operandSecond] = operands;

  const { operator, result } = React.useMemo(() => {
    const operator = operandFirst > operandSecond ? '-' : '+';
    const result = {
      '+': operandFirst + operandSecond,
      '-': operandFirst - operandSecond,
    }[operator];
    return { operator, result };
  }, []);
  const checkResult = sum.current ? +sum.current?.innerText.replace(/\s/g, '') === result : false;

  React.useEffect(() => {
    async function update() {
      updateResult(id, checkResult);
    }
    update();
  });

  return (
    <div className="calculation" style={{ background: finished && !checkResult ? 'orchid' : 'transparent' }}>
      <div ref={summandFirst}>
        <CompNumber value={String(operandFirst)} />
      </div>
      <div className="operator">
        {operator}
        {/* + */}
        {/* - */}
        {/* &#x00d7; */}
      </div>
      <div ref={summandSecond}>
        <CompNumber value={String(operandSecond)} />
      </div>
      <div
        style={{
          width: '100%',
          height: 10,
          background: '#0051f5',
          color: 'transparent',
          margin: '16px 0px',
        }}
      >
        ==
      </div>
      <div ref={sum}>
        <CompNumber value={String(result)} mask={'x'.repeat(Math.ceil(Math.log10(Math.max(...operands))) + 1)} />
      </div>
    </div>
  );
};

export const CompAddition = connectEvaluation(FC);
