import React from 'react';

interface ICompDigitProps extends React.SVGProps<SVGSVGElement> {
  letter: string;
  draggable: boolean;
  onDragStart?: any;
  onDragEnd?: any;
}

export const CompLetter: React.FC<ICompDigitProps> = ({ letter, draggable, onDragStart, onDragEnd, ...props }) => {
  return (
    <div draggable={draggable} onDragStart={onDragStart} onDragEnd={onDragEnd} className="letter">
      {letter}
    </div>
  );
};
