export namespace Util {
  export const trace = (message: any) => {
    const log = `${(window.performance.now() / 1000).toFixed(3)}: ${String(message)}\n`;
    console.warn(log);
    const logdiv = document.getElementById('log');
    logdiv && (logdiv.innerText = log + logdiv.innerText);
  };

  export const requestFullscreen = (element: any) => {
    const request =
      element.requestFullscreen ||
      element.webkitRequestFullscreen ||
      element.mozRequestFullScreen ||
      element.msRequestFullscreen;
    request && request.apply(element);
  };

  export function* shuffleGenerator(size: number) {
    const pool = [...Array(size)].map((_, i) => i);
    let poolRandom: number[] = [];
    while (true) {
      if (poolRandom.length === 0) {
        poolRandom = [...pool].sort(() => Math.random() - 0.5);
      }
      yield poolRandom.pop();
    }
  }

  export const random = (min: number, max: number) => ~~(Math.random() * (max - min)) + min;

  export const cloneDigit = (element: any, validDigit: string, updateScore: any) => {
    const digitElement = element.cloneNode(true);
    const check = digitElement.innerText === validDigit;
    digitElement.style.background = check ? 'green' : 'red';
    digitElement.ondragend = (event: any) => {
      const panel = document.createElement('div');
      panel.className = 'digit-panel';
      event.target.replaceWith(panel);
    };
    updateScore(~~check - 1);
    return digitElement;
  };

  export const cloneLetter = (element: any, valid: string, updateScore: any) => {
    const digitElement = element.cloneNode(true);
    const check = digitElement.innerText === valid;
    digitElement.style.background = check ? 'green' : 'red';
    digitElement.ondragend = (event: any) => {
      const panel = document.createElement('div');
      panel.className = 'letter-panel';
      event.target.replaceWith(panel);
    };
    updateScore(~~check - 1);
    return digitElement;
  };
}
