import React from 'react';
import { Util } from '../common/utils';
import { Asset } from '../common/defines';
import { connectEvaluation } from '../common/redux';

interface ICompDigitPanelProps extends React.SVGProps<SVGSVGElement> {
  session: number;
  random: any;
  onDragOver: any;
  onDrop: any;
}

const FC: React.FC<ICompDigitPanelProps> = ({ session, random, onDragOver, onDrop, ...props }) => {
  const background = React.useMemo(() => Asset.IMAGES[random.next().value], [session]);
  return (
    <div onDragOver={onDragOver} onDrop={onDrop}>
      <div
        className="digit-panel"
        style={{
          backgroundImage: `url(${process.env.PUBLIC_URL}/images/${background})`,
        }}
      />
    </div>
  );
};

export const CompDigitPanel = connectEvaluation(FC);
