import React from 'react';
import { connectDigit } from '../common/redux';
import { CompDigit } from './CompDigit';
import { CompDigitPanel } from './CompDigitPanel';

interface ICompOperandProps extends React.SVGProps<SVGSVGElement> {
  value: string;
  mask?: string;
  targetPanel: any;
}

const FC: React.FC<ICompOperandProps> = ({ value, mask = value, targetPanel, ...props }) => {
  const number = React.useRef<HTMLDivElement>(null);
  value = ('x'.repeat(mask.length) + value).slice(-mask.length);
  // console.log({ value, mask });

  return (
    <div ref={number} style={{ display: 'flex', justifyContent: 'flex-end' }}>
      {[...mask].map((digit, i) =>
        isFinite(+digit) ? (
          <CompDigit key={i} digit={+digit} draggable={false} />
        ) : (
          <CompDigitPanel
            key={i}
            onDragOver={(event: any) => event.preventDefault()}
            onDrop={(event: any) =>
              targetPanel({
                validDigit: value[i],
                panel: event.currentTarget,
              })
            }
          />
        )
      )}
    </div>
  );
};

export const CompNumber = connectDigit(FC);
