import React from 'react';
import { connectLetter, connectEvaluation } from '../common/redux';
import { CompLetterPanel } from './CompLetterPanel';
import { CompSpeaker } from './CompSpeaker';
import { CompRecorder } from './CompRecorder';

interface ICompWordProps extends React.SVGProps<SVGSVGElement> {
  word: string;
  pickPanel: any;
}

const FC: React.FC<ICompWordProps> = ({ word, pickPanel }) => {
  return (
    <div className="word">
      <div style={{ display: 'flex' }}>
        <div
          className="word-picture"
          style={{
            backgroundImage: `url(${process.env.PUBLIC_URL}/wild-animals/${word}-150x150.png)`,
          }}
        />
        <div style={{ display: 'flex', flexDirection: 'column' }}>
          <CompSpeaker word={word} />
          <CompRecorder word={word} />
        </div>
      </div>
      <div className="word-panel">
        {[...word.toUpperCase()].map((letter, i) => (
          <CompLetterPanel
            key={i}
            onDragOver={(event: any) => event.preventDefault()}
            onDrop={(event: any) => {
              const panel = event.currentTarget;
              panel.valid = letter;
              pickPanel(panel);
            }}
          />
        ))}
      </div>
    </div>
  );
};

export const CompWord = connectEvaluation(connectLetter(FC));
