import React from 'react';
import { connectEvaluation } from '../common/redux';
import { Util } from '../common/utils';

interface ICompEvaluationProps extends React.SVGProps<SVGSVGElement> {
  finished: boolean;
  results: any;
  penalty: any;
  newSession: any;
  submit: any;
}

const FC: React.FC<ICompEvaluationProps> = (props) => {
  const { results, penalty, newSession, finished, submit } = props;
  return (
    <div
      style={{
        position: 'fixed',
        top: 200,
        right: 0,
        width: 180,
        paddingRight: 40,
        textAlign: 'center',
        zIndex: 999,
      }}
    >
      <div
        style={{
          fontWeight: 'bold',
          margin: 16,
          cursor: 'pointer',
          height: 96,
          backgroundImage: `url(${process.env.PUBLIC_URL}/reset.png)`,
          backgroundSize: 'contain',
          backgroundRepeat: 'no-repeat',
          backgroundPosition: 'center',
        }}
        onClick={newSession}
      />

      <div
        style={{
          fontFamily: 'SaucerBB',
          fontSize: 80,
          color: finished ? 'red' : 'grey',
        }}
      >
        {100 + penalty}
      </div>
      <div
        style={{
          fontWeight: 'bold',
          backgroundColor: 'yellow',
          padding: 4,
          border: '2px solid blue',
          cursor: 'pointer',
          pointerEvents: finished ? 'none' : 'auto',
        }}
        onClick={() => {
          const mark = results.reduce((mark: number, result: boolean) => {
            return mark + (100 / results.length) * ~~result;
          }, 0);
          submit(Math.ceil(mark) - 100);
        }}
      >
        SUBMIT
      </div>
    </div>
  );
};

export const CompEvaluation = connectEvaluation(FC);
