import React from 'react';
import { connectEvaluation } from '../common/redux';

interface ICompSpeakerProps extends React.SVGProps<SVGSVGElement> {
  word: string;
}

export const CompSpeaker: React.FC<ICompSpeakerProps> = (props) => {
  const { word } = props;
  const audio = React.useMemo(() => {
    const wordIndex = `${word}${'_'.repeat(word.length)}`;
    const audioEl = document.createElement('audio');
    const sourceEl = document.createElement('source');
    sourceEl.src =
      `https://www.oxfordlearnersdictionaries.com/media/american_english/us_pron/` +
      `/${wordIndex.slice(0, 1)}` +
      `/${wordIndex.slice(0, 3)}` +
      `/${wordIndex.slice(0, 5)}` +
      `/${word}__us_1.mp3`;
    sourceEl.type = 'audio/mpeg';
    audioEl.appendChild(sourceEl);
    return audioEl;
  }, [word]);

  return (
    <div
      style={{
        width: 180,
        textAlign: 'center',
        zIndex: 999,
      }}
    >
      <div
        style={{
          fontWeight: 'bold',
          margin: 16,
          cursor: 'pointer',
          height: 96,
          backgroundImage: `url(${process.env.PUBLIC_URL}/speaker.png)`,
          backgroundSize: 'contain',
          backgroundRepeat: 'no-repeat',
          backgroundPosition: 'center',
        }}
        onClick={() => audio.play()}
      />
    </div>
  );
};
