import React, { SVGProps, useState, useEffect } from 'react';
import { connect } from 'redux-zero/react';
import Draggable from 'react-draggable';
import { IPageProps } from '.';
import { CompLetter } from '../components/CompLetter';
import { CompAlphabet } from '../components/CompAlphabet';
import { CompWord } from '../components/CompWord';
import { CompEvaluation } from '../components/CompEvaluation';
import { CompAddition } from '../components/CompAddition';
import { connectDigit, connectEvaluation } from '../common/redux';
import { Util } from '../common/utils';
import { Asset } from '../common/defines';
import logo from './draggable_4.svg';

interface IPageEnglishProps extends IPageProps {
  finished: boolean;
  session: number;
  digitPanel: any;
  targetPanel: any;
  updateScore: any;
}

const FC: React.FC<IPageEnglishProps> = (props) => {
  const [_, refresh] = React.useState(0);
  const { finished, session, digitPanel, targetPanel, updateScore } = props;
  const word = React.useMemo(() => Asset.WILD_ANIMALS[Util.random(0, Asset.WILD_ANIMALS.length)], [session]);
  return (
    <div key={session}>
      <CompAlphabet />
      <CompEvaluation />
      <CompWord word={word} />
    </div>
  );
};

export const PageEnglish = connectEvaluation(connectDigit(FC));
